import React from "react";
import Navigation from "../components/Navigation/Navigation";
import MovieGrid from "../components/MovieGrid/MovieGrid";
import axios from "axios";
import CircularProgress from "@material-ui/core/CircularProgress";
import Container from '@material-ui/core/Container';


class Recent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      listMovie: [],
      searchTerm: " ",
      prop:props
    };
    this.onSearch = this.onSearch.bind(this);
    this.change = this.change.bind(this);
    this.propChange =this.propChange.bind(this);
  }
  componentDidMount() {
   

    this.setState({loading:true},()=>{
        axios
        .get("https://enigmatic-river-85070.herokuapp.com/latest")
        .then((response) => response.data[0])
        .then((movies) => this.setState({ listMovie: movies,loading:false }));
    })
  }

  change(e) {
   
    this.setState({
      searchTerm: e.target.value,
    });
  }
  propChange(key){
    const props= this.state.prop;
          
             props.history.push('/download',{movieid:key})

  }
  onSearch(e) {
    const query = this.state.searchTerm;
    if (e.key === "Enter") {
      if (query) {
        this.setState({ loading: true }, () => {
          axios.get(`https://enigmatic-river-85070.herokuapp.com/quick_browse?name=${query}`)
        .then(response=>response.data[0])
        .then(movies=>this.setState({listMovie:movies,loading:false}))
        });
      }
    }
  }
  render() {
    try{
    const { loading } = this.state;
    return ( 
      <div>
        <Navigation   onBrowse={this.onSearch} onTextChange={this.change} textValue={this.state.searchTerm} />

        {loading ? (
         
         <Container maxWidth="sm" style={{textAlign:"center",paddingTop:"20%"}} >
           <CircularProgress style={{color:"#77d8d8"}}  size={100} />
         </Container>
         
        ) : (
          <div>
            <MovieGrid propChange={this.propChange}  movies={this.state.listMovie} />
          </div>
        )}
      </div>
    );
        }catch{
          this.prop.history.path('/error')
        }
  }
}

export default Recent;
