import React from 'react'
import Grid from '@material-ui/core/Grid';
import Details from '../Detail/Details'
import Star from '@material-ui/icons/Favorite';
import './MovieDetails.css';
function MovieDetails(props) {
  const genres=props.details.genres
  let genre_col='';
  if(genres){
   
    for(let i=0;i<genres.length;i++){
      if(i===genres.length-1){
        genre_col+=`${genres[i]}`
      }else{
        genre_col+=`${genres[i]}/`
      }
    
    }
  }

    return (
    
                <Grid item lg={4} xs={12} md={4} className="Text" >
                <h1 style={{ fontSize: "2.5em" }}>{props.details.title}</h1>
                <p style={{ fontSize: "1.3em" }}>{props.details.year}</p>
    <p style={{ fontSize: "1.2em",width:'100%' ,overflowX:'hidden'}}>{genre_col}</p>
<Details  identifier={<Star style={{color:"#4cbbb9",marginRight:".5em",paddingTop:".2em"}} />} value={props.details.like_count} />
<Details identifier={
  <img
    style={{ paddingTop: "0em" }}
    src="data:image/png;base64,"
    width="35"
    height="40"
  />
}
value={props.details.rating} 
/>

                  
</Grid>

    )
}

export default MovieDetails
