import React from 'react'
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import ComboBox from './assets/CombBox';
import TextField from '@material-ui/core/TextField';
import {makeStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
const usestyles= makeStyles({
    combo:{
        margin:"1.1em"
    },
    container:{
      marginTop:"2.5em"
    },
    input:{
        borderColor:'green'
    }
})


function Searchtxt(props) {
    const classes=usestyles()
    const selectOption=[{key:'Genre',value:[ "all","action", "adventure", "animation", "biography", "comedy", "crime", "documentary", "drama", "family", "fantasy", "film-noir", "game-show", "history", "horror", "music", "musical", "mystery", "news", "reality-tv", "romance", "sci-fi", "sport", "talk-show", "thriller", "war","western"
],action:props.ongenreChange},{key:'Quality',value:["all",'720p','1080p','3D','2160p'],action:props.onqualityChange},{key:'Order By',value:[ "latest", "oldest", "featured", "seeds", "peers", "year", "rating", "likes","alphabetical","downloads"],action:props.onorderChange}]
    const combos = selectOption.map(combo=><Grid item xs={12} sm={3}  lg={3} className={classes.combo} ><ComboBox label={combo.key} action={combo.action} value={combo.value} /> </Grid>)
    
    
    
    
    return (


        
            <Container className={classes.container} style={{display:"flex"}}  maxWidth="sm" >
                
                    <Grid item sm={0} md={0} lg={1}></Grid>
                    <Grid item sm={12} md={12} lg={10} > 
                    <TextField  className={{input:classes.input}} onChange={props.ontextChange} variant="outlined" fullWidth placeholder="Search...." />
                    
                    <div>
                        <Grid container>
                            
                           {combos}
                           <Grid item xs={2} lg={3} />
                           <Grid item xs={8} lg={6} style={{textAlign:"center"}} >
                           <Button style={{background:"#77d8d8"}} onClick={props.onSearch} fullWidth variant="contained" color="primary">Search</Button>
                           </Grid>
                           <Grid xs={2}  item lg={3} />
                        </Grid>
                    </div>
                    </Grid>
                    <Grid item sm={0} md={0} lg={1}></Grid>
               
               
            </Container>
      
    )
}

export default Searchtxt
