import React from 'react'
import Grid from '@material-ui/core/Grid';
import { useHistory } from "react-router-dom";
function Img(props) {
 
  const history = useHistory();
    return (
        <Grid
        item
        style={{ textAlign: "center", marginTop: "1.1em" }}
        lg={6}
        md={6}
        onClick={()=> {history.push('/download',{movieid:props.id});window.location.reload();}}
        xs={6}
        
      >
        <img  
            alt="No Img Found"
          style={{ borderRadius: ".3em",backgroundSize:"100% 100%" }}
          src={props.img}
          height="150"
          width="110"
        />
      </Grid>
    )
}

export default Img
