import React from "react";
import axios from "axios";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import "typeface-roboto";
//Components
import MainImg from "./MainImg/MainImg";
import MovieDetails from "./MovieDetails/MovieDetails";
import Similar from "./Similar/Similar";
import Trailer from "./Trailer/Trailer";
import Synopis from "./Synopis/Synopis";
import Cast from "./Cast/Cast";
import Expansion from "./Expansion/Expansion";
import CircularProgress from "@material-ui/core/CircularProgress";
import Navigation from "../components/Navigation/Navigation";
class App extends React.Component {
  constructor(props) {
    super(props);
    try {
      this.state = {
        movieid: props.location.state.movieid,
        movie: [],
        loading: false,
        prop: props,
        suggestion: [],
      };
    } catch {}
  }

  componentDidMount() {
    this.setState({ loading: true }, () => {
      axios
        .get(
          `https://enigmatic-river-85070.herokuapp.com/movie_details?movie_id=${this.state.movieid}`
        )
        .then((response) => response.data)
        .then((data) => this.setState({ loading: false, movie: data }));
      axios
        .get(
          `https://enigmatic-river-85070.herokuapp.com/movie_suggestion?movie_id=${this.state.movieid}`
        )
        .then((response) => this.setState({ suggestion: response.data }));
      //.then(data=>this.setState({loading:false,suggestion:data}))
    });
  }
  render() {
    try {
      let torrents;
      if (this.state.movie.torrents) {
        torrents = this.state.movie.torrents.map((tor) => (
          <Expansion
            torrent={tor}
            name={this.state.movie.title}
            imdb={this.state.movie.imdb_code}
            time={this.state.movie.runtime}
          />
        ));
      }

      return (
        <div>
          {this.state.loading ? (
            <Container
              maxWidth="sm"
              style={{ textAlign: "center", paddingTop: "20%" }}
            >
              <CircularProgress style={{ color: "#77d8d8" }} size={100} />
            </Container>
          ) : (
            <div>
              <Navigation />
              <div style={{ marginTop: "5em" }}>
                <Grid container>
                  <MainImg img={this.state.movie.large_cover_image} />
                  <MovieDetails details={this.state.movie} />
                  <Similar suggestion={this.state.suggestion} />
                </Grid>
                <Grid container>
                  <Trailer
                    video={this.state.movie.yt_trailer_code}
                    img={this.state.movie.large_screenshot_image2}
                  />
                </Grid>
                <Grid container>
                  <Cast
                    synopis={this.state.movie.description_full}
                    casts={this.state.movie.cast}
                  />
                </Grid>
                <Grid lg={12} xs={12} style={{ margin: "8% 0 8% 0" }} container>
                  {torrents}
                </Grid>
              </div>{" "}
            </div>
          )}
        </div>
      );
    } catch {
          return(<h1 style={{textAlign:"center"}}>Error Occurred</h1>)
    }
  }
}

export default App;
