import React from 'react'
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Img from './assets/Img';
import './Similar.css';
function Similar(props) {
  let suggestion ;
  if(props.suggestion){
    suggestion = props.suggestion.map(suggest=><Img  id={suggest.id} img={suggest.medium_cover_image} />)
  }
    return (
        <Grid item lg={3} md={4}  xs={12} >
              <Container   >
                
                <p   style={{textAlign:"center",fontSize:"1.5em"}}  className="similar" >Similar Movies</p>
        
                <Grid container>
                   {suggestion}
                </Grid>
              </Container>
            </Grid>
    )
}

export default Similar
