import React from "react";
import GridList from "@material-ui/core/GridList";
import MovieCard from "../MovieCard/MovieCard";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

const usestyle = makeStyles({
  message: {
    textAlign: "center",
    fontSize:"2.1em"
  },
});

function MovieGrid(props) {
  const classes = usestyle();
  let component;
 
  let erro_message=false;
  if (props.movies.message === "Cannot Found") {
      erro_message=true;
    component = (
      <Container maxWidth="sm" style={{ textAlign: "center", paddingTop: "10%",height:"100%" }}>
        <Typography className={classes.message}  >
          Movie Is Not Available In Our Database
        </Typography>
      </Container>
    );
  } else if (props.movies.message === "Probhited") {
    erro_message=true;
    component = (
        <Container maxWidth="sm" style={{ textAlign: "center", paddingTop: "10%",height:"100%" }}>
        <Typography className={classes.message}  >
        You Searched For Probhited Content
        </Typography>
      </Container>
    );
  } else {
    erro_message=false;
    component = props.movies.map((movie) => (
      <MovieCard key={movie.id} item={movie.id} propChange={props.propChange} rating={movie.rating} title={movie.title_english} img={movie.large_cover_image}/>
    ));
  }


  return (
   
   
!erro_message ?  <GridList style={{ margin: " 0 2em 0 3em " }} cols={3}>{component}</GridList>:component

    
   
    
   
  );
}
export default MovieGrid;
