import React from "react";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import GridListTitle from "@material-ui/core/GridListTile";
import Typography from "@material-ui/core/Typography";
import {makeStyles} from '@material-ui/core/styles';
import Star from '@material-ui/icons/Star';
const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 245,
    margin:"2em",
    height:450
  },
  typography:{
      textAlign:"center",
      height:"1.5em",
      overflow:"hidden",
      fontWeight:"bold",
      color:"#282c34"
  },
  ratings:{
    textAlign: "center", 
    margin: "5% 0 5% 0",
    color:"#4cbbb9"
  }
}));


function MovieCard(props) {

    const classes= useStyles();
    const nullArray = [];
    const rating  = Math.round(props.rating)
    for(let i=0;i<rating;i++){
      nullArray.push([i])
    }
    const stars  =nullArray.map(star=><Star key={star} ></Star>)
    console.log(props)
  return (
    <div key={props.key}>
      <Card className={classes.root} onClick={()=>{props.propChange(props.item)}} >
        <img src={props.img}   alt="No Img Found"  width="250" height="330" alt="This supposed to be poster" />
        <CardContent>
          <Typography className={classes.typography} variant="body2" color="textSecondary" component="p">
            {props.title}
          </Typography>
          <p className={classes.ratings}>
              {stars}
          </p>
        </CardContent>
      </Card>
    </div>
  );
}

export default MovieCard;
