import React from 'react'
import Navigation from '../components/Navigation/Navigation';
import Searchtxt  from '../components/SearchHolder/SearchTxt/Searchtxt';
import axios from 'axios';
import MovieGrid from '../components/MovieGrid/MovieGrid';
import Container from '@material-ui/core/Container';
import CircularProgress from '@material-ui/core/CircularProgress'
class Browser extends React.Component{
    constructor(props){
super(props)

this.state={
    searchTerm:"",
    genre:'',
    quality:'',
    order:'',
    listMovie:[],
    loading:false,
    prop:props
}
this.onSearch=this.onSearch.bind(this);
this.textChange=this.textChange.bind(this);
this.genreChange=this.genreChange.bind(this);
this.qualityChange=this.qualityChange.bind(this);
this.orderChange=this.orderChange.bind(this);
this.propChange=this.propChange.bind(this);
    }
    onSearch(){
        this.setState({loading:true},()=>{
            axios.get(`https://enigmatic-river-85070.herokuapp.com/detailed_browse?query_term=${this.state.searchTerm}&genre=${this.state.genre}&quality=${this.state.quality}&order_by=${this.state.order}`).then(response=>response.data[0])
             .then(movies=>this.setState({listMovie:movies,loading:false}))

        })
    }

    textChange(e){
        this.setState({searchTerm:e.target.value})
    }
    genreChange(e){
        this.setState({genre:e.target.value})
       
    }
    qualityChange(e){
       this.setState({quality:e.target.value})
     
    }
    orderChange(e){
        this.setState({order:e.target.value})
       
    }
    propChange(key){
             const props= this.state.prop;
             console.log(key)
             props.history.push('/download',{movieid:key})

    }
    render(){
        try{
        const {loading}=this.state
        return(
           <div>
                <Navigation />
                <Searchtxt ontextChange={this.textChange} ongenreChange={this.genreChange} onqualityChange={this.qualityChange} onorderChange={this.orderChange}  onSearch={this.onSearch} />
               
                   { loading ? <Container maxWidth="sm"  style={{textAlign:"center",paddingTop:"10%"}} >
           <CircularProgress style={{color:"#77d8d8"}} size={100} />
         </Container>  :
                    <MovieGrid propChange={this.propChange} movies={this.state.listMovie} />
        }
           </div>
        )
    }catch{
        this.prop.history.path('/error')
    }
    }
}

export default Browser