import React from 'react'
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Folder from '@material-ui/icons/Folder';
import Movie from '@material-ui/icons/Movie';
import Clock from '@material-ui/icons/Timer';
import Subtitles from '@material-ui/icons/Subtitles';
import Seed from '@material-ui/icons/CloudDownload'
import Peer from '@material-ui/icons/CloudUpload';
import Button from '@material-ui/core/Button';
import AspectRatio from '@material-ui/icons/AspectRatio';
import './Expansion.css';
function Expansion(props) {
    function minsToHours(min){
      const hour = (min)/60;
      const rHour=Math.floor(hour);
      const minutes =Math.round ((hour-rHour)*60);
      return `${rHour}hr ${minutes}min`
    }
    const magnet=`magnet:?xt=urn:btih:${props.torrent.hash}&dn=${props.torrent.url}tr=udp%3A%2F%2Fglotorrents.pw%3A6969%2Fannounce&tr=udp%3A%2F%2Ftracker.openbittorrent.com%3A80&tr=udp%3A%2F%2Ftracker.coppersurfer.tk%3A6969&tr=udp%3A%2F%2Fp4p.arenabg.ch%3A1337&tr=udp%3A%2F%2Ftracker.internetwarriors.net%3A1337`
    return (
        <Container >
          <ExpansionPanel>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon style={{color:"#3b6978"}} />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography>{`${props.name} ${props.torrent.quality}`}</Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
                    <Container>
                    <Grid container>
                      <Grid item lg={2} md={2} xs={12} style={{display:"flex",justifyContent:"center"}} >
                        <Folder style={{margin:'.5em .2em 0 0 '}} />
                        <p>{props.torrent.size}</p>
                      </Grid>
                      <Grid item lg={2} md={2}  xs={12}  style={{display:"flex",justifyContent:"center"}} >
                        <AspectRatio style={{margin:'.5em .2em 0 0 '}} />
    <p>{props.torrent.type}</p>
                      </Grid>
                      <Grid item lg={2} md={2}  xs={12} style={{display:"flex",justifyContent:"center"}} >
                        <Clock style={{margin:'.5em .2em 0 0 '}} />
                          <p>{minsToHours(props.time)}</p>
                      </Grid>
                      <Grid item lg={2} md={2}  xs={12} style={{display:"flex",justifyContent:"center"}} >
                        <Subtitles style={{margin:'.5em .2em 0 0 '}} />
                        <a href={`https://yts-subs.com/movie-imdb/${props.imdb}`} target="blank" className="link" > <p>Subtitles</p></a>
                       
                      </Grid>
                      <Grid item lg={2} md={2}  xs={12} style={{display:"flex",justifyContent:"center"}} >
                        <Seed style={{margin:'.5em .2em 0 0 '}} />
                          <p>{props.torrent.seeds}</p>
                      </Grid>
                      <Grid item lg={2} md={2}   xs={12} style={{display:"flex",justifyContent:"center"}} >
                        <Peer style={{margin:'.5em .2em 0 0 '}} />
                        <p>{props.torrent.peers}</p>
                      </Grid>
                      <Grid container style={{marginTop:"2em"}} >
                      <Grid item lg={3} md={3}   xs={0} ></Grid>
                      <Grid item lg={3} md={3}   xs={12} style={{textAlign:"center",paddingBottom:"1em"}} >
                    
                      <a href={props.torrent.url}  >
                          <Button  variant="contained" style={{background:"#4cbbb9",color:"white"}} >Torrent Download</Button>
                      </a>
                      </Grid>
                      <Grid item lg={3} md={3}   xs={12} style={{textAlign:"center"}} >
                      <a href={props.magnet}  >
                      <Button variant="contained" style={{background:"#4cbbb9",color:"white"}} >Direct  Download</Button>
                      </a>
                     
                      </Grid>
                      <Grid item lg={3} md={3}    xs={0} ></Grid>
                      </Grid>
                     
                     
                    </Grid>
                    </Container>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Container>
    )
}

export default Expansion
